// table-white-space
.table-white-space {
  th,
  td {
    white-space: nowrap;
  }
}

// table th, td padding
.table {
  thead th {
    vertical-align: top;
  }
  th,
  td {
    padding: 1rem;
    vertical-align: middle;
  }
  //table heading font-size
  th {
    font-size: 0.85rem;
  }
}

// Table - inverse
.table-inverse {
  color: white;
  background-color: #cbf5ff;
}

// Table - hover animation
.table-hover-animation {
  background-color: #f8f8f8;
  thead {
    th {
      border-top: 2px solid #f8f8f8;
      border-bottom: 0;
      background-color: white;
    }
  }
  th,
  td {
    border: 0;
  }
  tbody {
    tr {
      transition: all 0.25s ease;
      background-color: white;
      &:hover {
        transform: translateY(-4px);
      }
    }
  }
}
